
.whiteframe-z1,.xen-data-table,.xen-data-table .xen-data-table-edit-container{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
table{width:100%;table-layout:fixed
}
.xen-data-table{background-color:#fff;width:100%
}
.xen-data-table .xen-data-table-header{height:64px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;padding:0 24px
}
.xen-data-table table{border-collapse:separate;border-top:1px solid rgba(0,0,0,0.12);border-spacing:0
}
.xen-data-table th{border-bottom:1px solid rgba(0,0,0,0.12);color:rgba(0,0,0,0.54);font-size:12px;font-weight:500;height:56px;text-align:right;vertical-align:middle;white-space:nowrap
}
.xen-data-table th .xen-order-by{font-size:16px;font-weight:600;position:relative;right:8px;transition:all 0.25s cubic-bezier(0.55, 0, 0.1, 1);vertical-align:middle
}
.xen-data-table th .xen-order-by.ascending{transform:rotate(180deg)
}
.xen-data-table th span{cursor:pointer;vertical-align:middle
}
.xen-data-table tr,.xen-data-table tr td{transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.xen-data-table.bordered td{border-bottom:1px solid rgba(0,0,0,0.12)
}
.xen-data-table.striped tbody>tr:nth-child(odd) td{background-color:#fafafa
}
.xen-data-table td{color:rgba(0,0,0,0.87);font-size:13px;height:48px;position:relative;text-align:right
}
.xen-data-table .xen-padding{padding-top:16px;padding-bottom:16px
}
.xen-data-table td,.xen-data-table th{border-left:none;border-right:none;border-top:none;padding-right:56px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;padding-top:12px;padding-bottom:12px
}
.xen-data-table td.text-center,.xen-data-table th.text-center{text-align:center;padding-left:8px;padding-right:8px
}
.xen-data-table td.text-right,.xen-data-table th.text-right{text-align:right;padding-left:8px
}
.xen-data-table td.text-left,.xen-data-table th.text-left{text-align:left;padding-right:8px
}
.xen-data-table th.xen-first-col,.xen-data-table td.xen-first-col{padding-left:24px;padding-right:0;text-align:left
}
@media screen and (max-width: 768px){
.xen-data-table th.xen-first-col,.xen-data-table td.xen-first-col{padding-left:16px
}
}
.xen-data-table th.xen-first-col i,.xen-data-table td.xen-first-col i{margin-left:8px;margin-right:0;right:0
}
.xen-data-table tr th.first-col i{margin-left:8px;margin-right:0;right:0
}
.xen-data-table tr th:last-child,.xen-data-table tr td:last-child{padding-right:24px
}
@media screen and (max-width: 768px){
.xen-data-table tr th:last-child,.xen-data-table tr td:last-child{padding-right:16px
}
}
.xen-data-table.hover tr:hover td{background-color:#EEEEEE
}
.xen-data-table .xen-checkbox i{display:block
}
.xen-data-table .xen-table-selection{padding:0;width:56px
}
.xen-data-table .xen-column-options .xen-select-container{float:right;width:152px
}
.xen-data-table .xen-editable{cursor:pointer
}
.xen-data-table .xen-editable span{border-bottom:1px dashed rgba(0,0,0,0.54);cursor:pointer;position:relative;right:48px
}
.xen-data-table .xen-data-table-edit-container{background-color:#fafafa;left:230px;padding:16px 16px 0 16px;position:absolute;z-index:12
}
.xen-data-table .xen-data-table-edit-container .xen-input-container{margin:1px
}
.xen-data-table .xen-edit-buttons{float:right
}
.xen-data-table .xen-edit-buttons .cancel{margin-left:0
}
.xen-data-table .xen-edit-buttons .save{margin-right:0
}
.xen-data-table .xen-table-edit-icon{color:rgba(0,0,0,0.54);position:absolute;right:56px;top:18px
}
.xen-data-table .xen-results-message{text-align:center
}
@media screen and (max-width: 768px){
.xen-data-table.responsive{box-shadow:none
}
.xen-data-table.responsive .xen-data-table-container{overflow:visible
}
.xen-data-table.responsive table{background-color:transparent;margin-bottom:0
}
.xen-data-table.responsive table>thead,.xen-data-table.responsive table>tfoot{display:none
}
.xen-data-table.responsive table>tbody{display:block
}
.xen-data-table.responsive table>tbody>tr{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12);border-radius:2px;display:block;margin-bottom:16px
}
.xen-data-table.responsive table>tbody>tr>td{background-color:#fff;display:block;padding:16px;text-align:right;vertical-align:middle
}
.xen-data-table.responsive table>tbody>tr>td:hover{background-color:#EEEEEE
}
.xen-data-table.responsive table>tbody>tr>td[data-title]:before,.xen-data-table.responsive table>tbody>tr>td[data-title]:before{content:attr(data-title);font-weight:500;float:left;padding-right:16px
}
}
.xen-data-table .xen-select-container{margin:16px 0
}
.xen-data-table span{text-align:left
}
.xen-table-edit-backdrop{height:100vh;left:0;position:fixed;top:0;width:100vw;z-index:11
}



