
.xen-toast-container{position:fixed;bottom:0;width:100vw;right:0;z-index:10;display:-ms-flexbox;display:flex
}
.xen-toast-container .xen-toast{color:#fff;background-color:#323232;height:48px;padding:14px 24px;min-width:288px;max-width:568px;margin:0 auto;display:-ms-flexbox;display:flex;-ms-flex-line-pack:justify;align-content:space-between;position:relative
}
@media (max-width: 768px){
.xen-toast-container .xen-toast{min-width:100%;max-width:100%
}
}
.xen-toast-container .xen-toast span{font-size:14px;line-height:22px;vertical-align:middle;white-space:nowrap;text-overflow:ellipsis;display:block;overflow:hidden
}
.xen-toast-container .xen-toast .xen-button-container{display:block;position:absolute;right:2px;top:2px;color:#fff
}
.toast-enter-active,.toast-leave-active{transition:transform 0.4s cubic-bezier(0, 0, 0.2, 1);transform:translateY(0px)
}
.toast-enter-active span,.toast-leave-active span{transition:opacity 0.4s cubic-bezier(0, 0, 0.2, 1) 0.1s;opacity:1
}
.toast-enter,.toast-leave-active{transform:translateY(48px)
}
.toast-enter span,.toast-leave-active span{opacity:0
}



