
.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2,.xen-select-option-container{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.xen-select-container{position:relative;margin:0 0 32px 0;height:30px
}
.xen-select-container.xen-no-margin{margin:0
}
.xen-select-container span{color:rgba(0,0,0,0.87)
}
.xen-select-container .xen-input-border{width:100%;height:1px;position:absolute;background-color:rgba(0,0,0,0.12);bottom:3px;left:0
}
.xen-select-container .xen-input-border.xen-theme-red{background-color:#f44336
}
.xen-select-container label{display:block;position:absolute;transform:translateY(0px);transition:all 375ms cubic-bezier(0.4, 0, 0.2, 1);color:rgba(0,0,0,0.54);pointer-events:none;font-size:16px;top:4px;transform-origin:0%
}
.xen-select-container.has-value label{transform:translateY(-20px) translateX(0px) scale(0.75)
}
.xen-select-container.focus .xen-input-border{height:2px;background-color:currentColor
}
.xen-select-container.focus label{color:#3f51b5
}
.xen-select-container.focus input,.xen-select-container.focus textarea{border-bottom:2px solid #3f51b5;margin-bottom:15px
}
.xen-select-container.xen-dense span{font-size:14px
}
.xen-select-container.xen-dense .xen-select-option{height:40px
}
.xen-select{-ms-flex-align:center;align-items:center;display:-ms-inline-flexbox;display:inline-flex;cursor:pointer;font-size:16px;width:100%;text-overflow:ellipsis;height:28px;overflow-x:hidden
}
.xen-select i{position:absolute;right:4px;top:4px;opacity:.54;color:rgba(0,0,0,0.87)
}
.xen-select span{width:calc(100% - 16px);white-space:nowrap;text-overflow:ellipsis;overflow-x:hidden
}
.xen-select-option-container{position:fixed;left:0;top:0;z-index:10;background-color:#fff;padding:8px 0;min-height:48px;max-height:256px;overflow:auto;transform-origin:50%
}
.xen-select-option{font-size:16px;height:48px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;padding:0 16px;cursor:pointer;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.xen-select-option:hover{background-color:#EEEEEE
}
.xen-select-backdrop{position:fixed;height:100vh;width:100vw;top:0;left:0;z-index:10
}
.xen-select-container.focus label{color:currentColor
}
.disabled{opacity:.75;pointer-events:none
}
.disabled .xen-input-border{background-color:transparent;border-bottom:1px dashed rgba(0,0,0,0.24)
}



