
.xen-checkbox-container{border-radius:50%;cursor:pointer;display:inline-block;height:42px;overflow:hidden;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;vertical-align:middle;width:42px;z-index:1
}
.xen-checkbox-container i{color:#fff;font-weight:700;font-size:18px;left:0;margin:12px;opacity:0;position:absolute;top:0;transition:all 265ms cubic-bezier(0.4, 0, 0.2, 1)
}
.xen-checkbox-container .xen-checkbox{border-color:rgba(0,0,0,0.54);border-radius:2px;border-style:solid;border-width:2px;height:22px;margin:10px;transition:all 265ms cubic-bezier(0.4, 0, 0.2, 1);vertical-align:middle;width:22px;z-index:6
}
.xen-checkbox-container.has-value i{opacity:1
}
.xen-checkbox-container.has-value .xen-checkbox{background-color:currentColor;border-color:currentColor
}



