
.xen-radio-button-container{width:42px;height:42px;display:inline-block;position:relative;vertical-align:middle;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden;border-radius:50%;z-index:1
}
.xen-radio-button-container .circle{width:10px;height:10px;border-radius:50%;position:relative;opacity:0;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.xen-radio-button-container .xen-radio-button{margin:10px;width:22px;height:22px;border-width:2px;border-style:solid;border-radius:50%;border-color:rgba(0,0,0,0.54);vertical-align:middle;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1);display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-line-pack:center;align-content:center;-ms-flex-pack:distribute;justify-content:space-around;overflow:hidden;pointer-events:none;color:currentColor
}
.xen-radio-button-container.has-value i{opacity:1;pointer-events:none
}
.xen-radio-button-container.has-value .xen-radio-button{border-color:currentColor
}
.xen-radio-button-container.has-value .xen-radio-button .circle{opacity:1;background-color:currentColor;border-color:currentColor
}
.xen-radio-button-container.has-value.secondary-color{color:#e91e63
}
.xen-radio-button-container.has-value.secondary-color .xen-radio-button{border-color:#e91e63
}
.xen-radio-button-container.has-value.secondary-color .xen-radio-button .circle{background-color:#e91e63;border-color:#e91e63
}



