
.xen-input-container{position:relative;width:100%;margin-bottom:16px
}
.xen-input-container.has-error-msg{margin-bottom:32px
}
.xen-input-container.xen-label{margin-top:32px
}
.xen-input-container .xen-input-border{width:100%;height:1px;position:absolute;background-color:rgba(0,0,0,0.12);bottom:16px
}
.xen-input-container .xen-input-border.xen-theme-red{background-color:#f44336
}
.xen-input-container label{display:block;position:absolute;transform:translateY(4px);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);color:rgba(0,0,0,0.54);pointer-events:none;font-size:16px;transform-origin:0%
}
.xen-input-container input{height:28px
}
.xen-input-container input,.xen-input-container textarea{background-color:transparent;outline:none;box-sizing:border-box;border-width:0;border-style:solid;border-color:rgba(0,0,0,0.12);padding:4px 0;font-size:16px;width:100%;max-width:100%;margin-bottom:16px;font-family:'Roboto'
}
.xen-input-container.focus label,.xen-input-container.has-value label{transform:translateY(-16px) translateX(0) scale(0.75)
}
.xen-input-container.focus label{color:currentColor
}
.xen-input-container.focus label.xen-color-red{color:#f44336
}
.xen-input-container.focus .xen-input-border{width:100%;height:2px;position:absolute;background-color:currentColor;bottom:14px
}
.xen-input-container.focus .xen-input-border.xen-theme-red{background-color:#f44336
}
.xen-textarea{margin-bottom:16px
}
.xen-textarea textarea{border-bottom:1px solid rgba(0,0,0,0.12)
}
.xen-textarea.focus textarea{border-bottom:2px solid #3f51b5
}
.xen-input-container.xen-disabled input{opacity:.75
}
.xen-input-container.xen-disabled .xen-input-border{background-color:transparent;border-bottom:1px dashed rgba(0,0,0,0.24)
}
.xen-input-container.xen-disabled textarea{border-bottom:1px dashed rgba(0,0,0,0.24)
}
.xen-placeholder{color:rgba(0,0,0,0.54)
}
::-webkit-input-placeholder,:-moz-placeholder,::-moz-placeholder,:-ms-input-placeholder{color:rgba(0,0,0,0.54)
}
.xen-input-error{position:absolute;font-size:12px;bottom:-4px
}
input[type='number']{-moz-appearance:textfield
}
input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none
}



