
.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2,.xen-icon-button-container.raised{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3,.xen-button-container.raised{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4,.xen-button.raised:hover{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5,.xen-button.raised:focus{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.xen-button{background-color:transparent;border:none;border-radius:2px;cursor:pointer;display:inline-block;font-size:14px;font-weight:500;line-height:20px;position:relative;padding:8px 16px;outline:none;text-transform:uppercase;transition:all 375ms cubic-bezier(0, 0, 0.2, 1);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;z-index:1
}
.xen-button:hover{background-color:rgba(0,0,0,0.08)
}
.xen-button.disabled{cursor:not-allowed;opacity:.54;pointer-events:none
}
.xen-button.flat.primary{color:#3f51b5
}
.xen-button.flat.secondary{color:#e91e63
}
.xen-button.flat:hover{background-color:rgba(0,0,0,0.08)
}
.xen-button.raised:hover{background-color:rgba(0,0,0,0.08)
}
.xen-button.raised.primary{background-color:#3f51b5;color:#fff
}
.xen-button.raised.secondary{background-color:#e91e63;color:#fff
}
.xen-button.xen-icon-button{border-radius:50%;height:40px;padding:0;margin:0;width:40px
}
.xen-button.xen-icon-button i{vertical-align:middle
}
.xen-button.xen-icon-button.small{height:40px;width:40px
}
.xen-button.xen-icon-button.square{height:48px;width:48px;border-radius:2px;-webkit-mask-image:none
}
.xen-button-container{position:relative;display:inline-block;margin:4px;overflow:hidden
}
.xen-button-container.disabled{color:rgba(0,0,0,0.26);cursor:not-allowed
}
.xen-button-container.disabled.raised{background-color:rgba(0,0,0,0.12)
}
.xen-button-container.disabled>*{pointer-events:none
}
.xen-icon-button-container{border-radius:50%;display:inline-block;font-size:14px;height:40px;margin:4px;overflow:hidden;position:relative;transition:all 375ms cubic-bezier(0, 0, 0.2, 1);width:40px;z-index:1
}
.xen-icon-button-container.large,.xen-icon-button-container.large .xen-icon-button-shadow,.xen-icon-button-container.large .xen-icon-button-wrapper{height:56px;width:56px
}
.xen-icon-button-wrapper{cursor:pointer;display:-ms-flexbox;display:flex;height:40px;margin-right:8px;width:40px
}
.xen-icon-button-wrapper i{margin:auto;pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.xen-icon-button-container.disabled{color:rgba(0,0,0,0.26);cursor:not-allowed
}
.xen-icon-button-container.disabled.raised{background-color:rgba(0,0,0,0.12)
}
.xen-icon-button-container.disabled>*{pointer-events:none;transition:all 375ms cubic-bezier(0, 0, 0.2, 1)
}



