
.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.xen-tabs{width:100%
}
.xen-tabs>span{line-height:24px;white-space:nowrap
}
.xen-tabs.icons>.xen-tab-items-container .xen-tab-items>button,.xen-tabs.icons>.xen-tab-items-container .xen-tab-items>button span{line-height:14px
}
.xen-tabs.icons-only .xen-tab-items-container .xen-tab-items i{margin-bottom:0
}
.xen-tabs .xen-tab-items-container .xen-tab-items i{min-width:24px;min-height:24px
}
.xen-tab-items-container{overflow:hidden
}
.xen-tab-items-container span{text-overflow:ellipsis;overflow:hidden;display:block
}
.xen-tab-items-container .xen-tab-items{display:-ms-flexbox;display:flex;transition:transform 0.2s cubic-bezier(0, 0, 0.2, 1);position:relative
}
.xen-tab-items-container .xen-tab-items button{margin:0;padding:12px 16px;border-radius:0
}
.xen-tab-items-container .xen-tab-items.panning{transition:none
}
.xen-tab-items-container .xen-tab-items i{display:block;margin-bottom:10px
}
.xen-tab-items{display:inline-block
}
.xen-active-tab-bar{width:100px;height:2px;position:relative;top:-2px;transform-origin:0px 0px 0px;transition:all 0.2s cubic-bezier(0, 0, 0.2, 1);z-index:5;margin-bottom:-2px
}
.xen-active-tab-bar.panning{transition:none
}
.xen-active-tab-bar.xen-theme-white{background-color:#fff
}
.xen-button-container.xen-tab-button{margin:0;display:inline-table
}
.xen-button-container.xen-tab-button .xen-button{padding:10px 16px
}
.xen-tabs .xen-tab-icon{margin:auto;text-align:center;pointer-events:none
}
.xen-tab-text{pointer-events:none;display:block;position:relative
}



