
.xen-list{list-style:none;padding:0;margin:8px 0
}
.xen-list .xen-list-item{font-size:16px;width:100%;display:-ms-flexbox;display:flex;-ms-flex-negative:0;flex-shrink:0;-ms-flex-align:center;align-items:center;transition:background-color 0.2s cubic-bezier(0, 0, 0.2, 1);padding:0 16px;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;vertical-align:middle;cursor:pointer
}
.xen-list .xen-list-item.avatar{height:56px
}
.xen-list .xen-list-item:hover{background-color:rgba(0,0,0,0.09)
}
.xen-list .xen-list-item.bold{font-weight:500
}
.xen-list .xen-list-item .xen-list-item-text-container{vertical-align:middle;display:inline-block;width:100%;pointer-events:none
}
.xen-list .xen-list-item.two-line .xen-list-item-text-container,.xen-list .xen-list-item.multi-line .xen-list-item-text-container{padding:20px 0
}
.xen-list .xen-list-item.one-line .xen-list-item-text-container{padding:16px 0
}
.xen-list.dense .xen-list-item{font-size:14px;height:40px
}
.xen-list.dense .xen-list-item.avatar{height:48px
}
.xen-list .xen-list-item-icon{width:40px;opacity:.54;-ms-flex-negative:0;flex-shrink:0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default
}
.xen-list .xen-list-item-icon i{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.xen-list .xen-list-item-avatar{width:40px;height:40px;background-color:gray;opacity:.54;margin-right:16px;border-radius:50%;display:-ms-flexbox;display:flex;-ms-flex-negative:0;flex-shrink:0;-ms-flex-align:center;align-items:center
}
.xen-list .xen-list-item-avatar i{vertical-align:middle;text-align:center;width:100%;color:#fff
}
.xen-list .xen-item-text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;pointer-events:none
}
.xen-list .secondary-text{font-size:14px;color:rgba(0,0,0,0.54);white-space:nowrap;overflow:hidden;text-overflow:ellipsis;margin-top:2px;line-height:1
}
.xen-list span{line-height:1
}
.xen-list .two-line span,.xen-list .multi-line span{display:block
}
.xen-list .xen-list-item-text-container{white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.xen-list .xen-list-item.two-line{height:72px
}
.xen-list .xen-list-item.two-line .xen-list-item-avatar i{position:relative;top:8px
}
.xen-list .xen-list-item.two-line.avatar{height:72px
}
.xen-list .xen-list-item.multi-line{height:auto
}
.xen-list .xen-list-item.multi-line .secondary-text{white-space:normal;line-height:1.25
}
.xen-list .xen-list-item.multi-line .xen-list-item-avatar i{position:relative;top:8px
}
.xen-list .xen-list-item.multi-line.avatar{height:72px
}
.xen-list.dense .xen-list-item.two-line{height:60px
}
.xen-list.dense .xen-list-item.avatar{height:60px
}
ul{list-style:none;padding:0
}



