
.whiteframe-z1,.xen-dropdown-container{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.xen-dropdown-container{min-width:112px;max-width:280px;display:inline-block;position:absolute;background-color:#fff;overflow:hidden;opacity:0;z-index:10;top:0
}
.xen-dropdown-container.closed{opacity:0
}
.xen-dropdown-container.open{z-index:10
}
.xen-dropdown-container .list-item{transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.xen-dropdown-container .list-item:hover{background-color:rgba(0,0,0,0.12)
}
.xen-dropdown-backdrop{position:fixed;height:100vh;width:100vw;top:0;left:0;z-index:0
}
.xen-dropdown-wrapper{position:relative
}



